import { I_Scene } from '@containers/Home/types';
import { Typography } from 'antd';
import dynamic from 'next/dynamic';
import { memo, useMemo, useState } from 'react';
import { ListSceneWrapper } from './styled';

const { Text } = Typography;

const SearchContainer = dynamic(() => import('@components/SearchContainer'));

interface Props {
  listScene: I_Scene[];
  titleProject: string;
  sceneSelected?: I_Scene;
  onNextScene: (scene: I_Scene) => void;
}

const ListScene = memo(
  ({ listScene, titleProject, sceneSelected, onNextScene }: Props) => {
    const [searchValue, setSearchValue] = useState('');

    const listSceneFiltered = useMemo(
      () =>
        listScene.filter(item =>
          item.title.toLowerCase().includes(searchValue.trim().toLowerCase()),
        ) || [],
      [listScene, searchValue],
    );

    return (
      <ListSceneWrapper>
        <div className="--header">
          <div className="--title">
            <Text
              style={{ width: '100%' }}
              ellipsis={{ tooltip: titleProject }}
            >
              {titleProject}
            </Text>
          </div>
        </div>
        <div className="--search">
          <SearchContainer
            onTriggerSearch={({ value }) => setSearchValue(value)}
          />
        </div>
        <div className="--body hide-scrollbar">
          <div className="--list">
            {listSceneFiltered.map(item => (
              <div
                key={item.id}
                className={`--item ${
                  sceneSelected?.id === item.id ? 'active' : ''
                }`}
                onClick={onNextScene.bind(this, item)}
              >
                <div className="--left">
                  <div className="--thumb">
                    <img src={item.media?.src_thumb} alt={item.title} />
                  </div>
                </div>
                <div className="--right">
                  <div className="--info">
                    <div className="mainTxt">{item.title}</div>
                    <div className="subTxt">
                      <span>
                        {(item.commentScenes || [])
                          .filter(el => !el.parent)
                          .filter(el => !el.isResolved)?.length || 0}{' '}
                        /{' '}
                        {(item.commentScenes || []).filter(el => !el.parent)
                          .length || 0}{' '}
                        active comments
                      </span>
                    </div>
                  </div>
                </div>
              </div>
            ))}
          </div>
        </div>
      </ListSceneWrapper>
    );
  },
);

export default ListScene;
